import React from 'react';

const EventDurationInput = ({ duration, changeHandler }) => {
  return (
    <label className="d-block">
      Duration:
      <input
        type="number"
        value={duration}
        min="1"
        max="10"
        onChange={changeHandler}
        className="mx-2"
      />
    </label>
  );
};

export default EventDurationInput;



