import React from 'react';

const EventDateInput = ({ date, changeHandler }) => {
  return (
    <label className="d-block">
      Date:
      <input
        type="date"
        value={date}
        onChange={changeHandler}
        className="mx-2"
      />
    </label>
  );
};

export default EventDateInput;



