import React, { useState, useEffect } from 'react';
import eventService from './services/events';
import Notification from './components/Notification';
import Event from './components/Event';
import EventForm from './components/EventForm';

const App = () => {
  const [events, setEvents] = useState([]);

  const [eventName, setEventName] = useState('');
  const [eventDate, setEventDate] = useState(
    new Date().toISOString().substring(0, 10)
  );
  const [eventTime, setEventTime] = useState('00:00');
  const [eventDuration, setEventDuration] = useState(0);
  const [errorMessage, setErrorMessage] = useState(null);

  useEffect(() => {
    eventService.getAll().then(initialEvents => {
      setEvents(initialEvents);
    });
  }, []);

  const handleEventNameChange = event => {
    setEventName(event.target.value);
  };

  const handleEventDateChange = event => {
    setEventDate(event.target.value);
  };

  const handleEventTimeChange = event => {
    setEventTime(event.target.value);
  };

  const handleEventDurationChange = event => {
    setEventDuration(Number(event.target.value));
  };

  const handleEventNameUpdate = id => e => {
    const event = events.find(cEvent => cEvent.id === id);
    const changedEvent = { ...event, name: e.target.value };

    eventService
      .update(id, changedEvent)
      .then(returnedEvent => {
        setEvents(
          events.map(cEvent => (cEvent.id !== id ? cEvent : returnedEvent))
        );
      })
      .catch(error => {
        setErrorMessage(
          `Èvent '${event.name}' has been already deleted from the server.`
        );
        setTimeout(() => {
          setErrorMessage(null);
        }, 5000);
        setEvents(events.filter(cEvent => cEvent.id !== id));
      });
  };

  const eventDivs = () =>
    events.map(event => (
      <Event
        key={event.id}
        event={event}
        nameUpdateHandler={handleEventNameUpdate(event.id)}
      />
    ));

  const addEvent = e => {
    e.preventDefault();

    const newEvent = {
      name: eventName,
      date: eventDate + ' ' + eventTime,
      durationInHours: eventDuration
    };

    eventService.create(newEvent).then(returnedEvent => {
      setEvents(events.concat(returnedEvent));
      setEventName('');
      setEventDate('');
      setEventTime('');
      setEventDuration(0);
    });
  };

  return (
    <div className="container">
      <h1>Calendar</h1>
      <Notification message={errorMessage} />
      <EventForm
        name={eventName}
        date={eventDate}
        time={eventTime}
        duration={eventDuration}
        nameChangeHandler={handleEventNameChange}
        dateChangeHandler={handleEventDateChange}
        timeChangeHandler={handleEventTimeChange}
        durationChangeHandler={handleEventDurationChange}
        submitHandler={addEvent}
      />
      <ul>{eventDivs()}</ul>
    </div>
  );
};

export default App;



