import React from 'react';

const EventNameInput = ({ name, changeHandler }) => {
  return (
    <label className="d-block">
      Name:
      <input
        value={name}
        onChange={changeHandler}
        placeholder="Birthday party"
        pattern=".{6,20}"
        required
        title="6 to 20 characters"
        className="mx-2"
      />
    </label>
  );
};

export default EventNameInput;



