import React from 'react';

const EventTimeInput = ({ time, changeHandler }) => {
  return (
    <label className="d-block">
      Time:
      <input
        type="time"
        value={time}
        onChange={changeHandler}
        className="mx-2"
      />
    </label>
  );
};

export default EventTimeInput;



